#ranking-container{width:100%;height:100%;display:flex;flex-direction:row}#ranking-container .screen-box{width:1920px;height:1080px;display:flex;flex-direction:column;align-items:center;justify-content:center}#ranking-container .screen-box.left{--li-font-size: 90px;--li-line-height: 1;--li-padding: 15px;--ul-padding-right: 90px;--ul-min-width: 700px;--min-width: 1050px}#ranking-container .screen-box.right{--li-font-size: 45px;--li-line-height: 1;--li-padding: 7px;--ul-padding-right: 70px;--ul-min-width: 400px;--min-width: auto}#ranking-container .screen-box>div{min-width:var(--min-width);flex-direction:row;align-items:center}#ranking-container .screen-box ul{list-style:none;margin-top:20px;padding-right:var(--ul-padding-right);display:inline-block}#ranking-container .screen-box ul>li{line-height:var(--li-line-height);font-size:var(--li-font-size);padding:var(--li-padding) 0}#ranking-container .screen-box ul:nth-child(1){text-align:center}#ranking-container .screen-box ul:nth-child(2){min-width:var(--ul-min-width)}#ranking-container .screen-box ul:nth-child(3){text-align:right;padding-right:0px}#ranking-container .screen-box ul>li:nth-child(1){font-size:30px;opacity:0.5;line-height:60px}#ranking-container .screen-box .info{font-family:'BIZ UDPGothic', sans-serif;font-size:20px;line-height:1.5;opacity:0.5}#ranking-container .screen-box h2{font-size:60px}

.github-repo-info .full-name{font-size:45px;line-height:1.5;margin:0 0 35px}.github-repo-info .info{font-size:20px;opacity:0.5;margin:0 0 90px}.github-repo-info .info span:first-child{margin-right:40px}.github-repo-info .desc{font-size:24px;line-height:1.5;margin:0 0 45px;text-overflow:ellipsis;overflow:hidden;display:-webkit-box;-webkit-line-clamp:4;line-clamp:4;-webkit-box-orient:vertical}.github-repo-info .desc-jp-memo{font-family:'BIZ UDPGothic', sans-serif;font-size:20px;opacity:0.5;line-height:1.5}.github-repo-info .desc-jp{font-family:'BIZ UDPGothic', sans-serif;font-size:20px;line-height:1.5;text-overflow:ellipsis;overflow:hidden;display:-webkit-box;-webkit-line-clamp:4;line-clamp:4;-webkit-box-orient:vertical}

.tweet-info{min-height:368px}.tweet-info .user-name{font-family:'BIZ UDPGothic', sans-serif;font-size:24px;margin-bottom:18px;line-height:1;padding-right:10px}.tweet-info .screen-name{font-size:20px;opacity:0.5;margin-bottom:26px;line-height:1}.tweet-info .screen-name>span{padding-left:20px}.tweet-info .text{font-family:'BIZ UDPGothic',sans-serif;font-size:20px;line-height:1.5;min-height:90px}

